@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";
@import "../_drinks";

div.drink-prefs-hdr {
    align-items: center;
    //  rgb(255, 165, 0), rgb(220, 66, 230)
    background-image: kbrdBg($cat-prefs-c1, $cat-prefs-c2);
    display: flex;
    height: $gr-ll * 100%;
    justify-content: center;
    text-align: center;
}

ul.ings-wanted,
ul.ings-wanted-not {
    display: flex;
    flex-wrap: wrap;
    float: left;
    height: ($gr-h + $gr-lh) * 100%;
    justify-content: space-around;
    list-style: none;
    width: 50%;

    li.ings {
        flex-grow: 2;
        max-width: $gr-h * 100%;
        position: relative;
        width: 50%;
    }
}

ul.ings-wanted {
    background-color: $bg-cl;
}

ul.ings-wanted-not {
    background-image: $bg-d-img-gradient;
    color: $cl;
}
