@import "../../../app/app-variables";
@import "../../../app/app";
@import "../../_categories";
@import "./_category-aside";

@keyframes tracking-in-expand {
    0% {
        letter-spacing: -0.5em;
        opacity: 0;
    }
    40% {
        opacity: $gr-lh;
    }
    100% {
        opacity: $gr-l;
    }
}

@keyframes focus-in-expand {
    0% {
        letter-spacing: -0.5em;
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        filter: blur(0px);
        opacity: $gr-l;
    }
}

aside.cat-aside-narrow,
aside.cat-aside-wide {
    display: block;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    overflow: hidden;

    div.category-icon {
        height: $gr-ll * 100%;
        opacity: $gr-l;

        span.category-icon {
            animation: rotateThis 1s linear;
        }
    }

    h2.category-name {
        animation: tracking-in-expand 1.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    nav.cat-aside-narrow,
    nav.cat-aside-wide {
        background-image: nav-bg-img(rgb(106, 36, 180), rgb(131, 46, 217));
        bottom: 0;
        height: $gr-ll * 100%;
        position: absolute;
        width: 100%;
    }

    div.cat-aside {
        color: $cl;
        display: flex;
        flex-wrap: wrap;
        height: $cat-aside-space-h;
        justify-content: center;
    }
}

aside.cat-aside-narrow {
    background-image: aside-narr-bg-img(hsl(98, 0%, 0%), hsl(98, 0%, 0%));

    width: $cat-narr-w;

    div.category-icon {
        span.category-icon {
            height: $gr-hhh * 100%;
        }
    }

    h2.category-name {
        height: ($gr-h + $gr-lll + $gr-lllh) * 100%;
        position: relative;
        width: 100%;
        white-space: nowrap;

        span.category-name {
            transform: rotate(90deg);
            transform-origin: center;
        }
    }

    div.cat-aside-narrow-spacer {
        display: none;
    }
}

aside.cat-aside-wide {
    background-image: aside-wide-bg-img($bg-cd, rgba(7, 7, 7, 0.9));
    width: $cat-wide-w;

    div.category-icon {
        span.category-icon {
            height: $gr-hhh * 100%;
        }
    }

    h2.category-name {
        height: $gr-ll * 100%;
        margin: 0 auto;
    }

    div.cat-aside-wide-spacer {
        height: $cat-aside-space-h;
    }

    div.cat-aside {
        margin: 0 auto;
        width: $gr-hhl * 100%;

        div.drinks-count {
            height: $gr-h * 100%;
        }

        button {
            display: inline-block;
            height: $gr-llh * 100%;
            position: relative;
            width: $gr-hhh * 100%;
        }
    }
}
